rder-left: 2px solid gray;\r\n        border-right: 2px solid gray;\r\n        line-height: 50px;\r\n           font-size: 30px;\r\n        font-weight: bold;\r\n    }\r\n    \r\n    \r\n</style>"],"sourceRoot":"webpack://"}]);

